






$(function(){
    //游戏开始页面
    (function($){
        $('.stage2 .button1').on('click',e=>{
            $('.stage2').hide()
            $('.stage3').show()
            gamelogic()
        })
        $('.stage2 .button2').on('click',e=>{
            $('.stage1').show()
        })
    })(jQuery);
    //游戏规则页面
    (function($){
        $('.stage1 .backbutton').on('click',e=>{
            $('.stage1').hide()
        })
    })(jQuery)
})


function gamelogic(){
    let gameIndex = 0
    let isClick = false
    $('.nextbtn1').on('click',e=>{
        nextBtn()
        if(gameIndex<gameData.length){
            if($.trim($('.stage3 .gameans').val())==gameData[gameIndex].gameans){
                showRight()
            }else{
                showWrong()
            }
            isClick = false
        }
    })
    $('.resetbtn').on('click',e=>{
        $('.showelem').hide()
        $('.showelem1').show()
    })
    $('.nextbtn2').on('click',e=>{
        if(isClick){return}
        gameIndex++
        nextBtn()
        $('.showelem').hide()
        $('.showelem1').show()
        isClick  = true
    })
    $('.nextbtn3').on('click',e=>{
        if(isClick){return}
        gameIndex++
        nextBtn()
        $('.showelem').hide()
        $('.showelem1').show()
        isClick  = true
    })
    var showRight = function(){
        $('.showelem').hide()
        $('.showelem2').show()
        gameData[gameIndex].right = true
    }
    var showWrong = function(){
        $('.showelem').hide()
        $('.showelem3').show()
        gameData[gameIndex].right = false
    }
    var nextBtn = function(){
        if(gameData[gameIndex]){
            showTime()
            $('.stage3 .gameask').text(gameData[gameIndex].gameask)
            $('.stage3 .gametip').text('（'+gameData[gameIndex].gametip+'）')
        }else{
            let result = {}
            result.usetime = gameData.
                map(data=>30-data.time).
                reduce((prev,curr)=>prev+curr)
            result.rightcount = gameData.
                map(data=>data.right).
                filter(data=>data).
                length

            if(window.callback)window.callback(result)
        }
    }
    var showTime = (function(){
        var elem = $('.showTime')
        var timmer = null
        var chinaNum = ['一','二','三','四','五','六','七','八','九','十']
        var indexnumber = $('.indexnumber')
        return function(){
            clearInterval(timmer)
            elem.text(30)
            if(gameData[gameIndex]){
                indexnumber.text(chinaNum[gameIndex])
                timmer = setInterval(function(){
                    if(gameData[gameIndex]&&gameData[gameIndex].time>0){
                        elem.text(--gameData[gameIndex].time)
                    }
                },1000)
            }
        }
    })()
    showTime()
    $('.stage3 .gameask').text(gameData[gameIndex].gameask)
    $('.stage3 .gametip').text('（'+gameData[gameIndex].gametip+'）')
}




